@import url('https://fonts.googleapis.com/css2?family=Albert+Sans:ital,wght@0,100..900;1,100..900&family=Cedarville+Cursive&family=DM+Serif+Display:ital@0;1&family=Montserrat:ital,wght@0,100..900;1,100..900&family=Radio+Canada:ital,wght@0,300..700;1,300..700&family=Raleway:ital,wght@0,100..900;1,100..900&display=swap');

*{
  padding  :0;
  margin :0;
  box-sizing: border-box;  font-family: "Albert Sans", sans-serif;
  box-sizing: border-box;  
}

@font-face {
    font-family: 'MAZIUS REVIEW 20.09';
    src: url('./components/fonts/MAZIUSREVIEW2009-Regular.woff2') format('woff2'),
        url('./components/fonts/MAZIUSREVIEW2009-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}


body{
  background-color: #000;
  color :white;
  max-width: 100vw;
  box-sizing: border-box;
  overflow-x: hidden;
}
html {
  scroll-behavior: smooth;
}
.center{
  text-align: center;
}
.upperMargin {
  margin-top: 200px;
}

.headingGap{
  margin  : 30px 0;
}
.descriptionGap{
  margin-bottom : 80px ;
}
.containerWidth{
  width: 83%;
  margin : auto;
}
.gap{
  margin: 100px 0 0;
}

@media screen and (max-width: 1024px) {
  body{
    padding: 0 15px;
  }
}

@media screen and (max-width: 600px) {
  .headingGap {
    margin: 16px 0;
}
.paragraph{
  font-size: 11px !important;
}

.descriptionGap {
  margin-bottom: 30px;
}
}



